* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  overflow-x: hidden;

  margin-right: calc(-1 * (100vw - 100%));
}

body {
  height: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

.main-grid {
  display: flex;
  min-height: 100vh;
}